/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* ----- */

* {
  box-sizing: border-box;
}

body {
  color: #0c0e16;
  font-size: 0.75rem;
  line-height: 15px;
  letter-spacing: -0.25px;
  font-weight: 600;
  overflow: hidden;
}

h1,
h2,
h3,
h4 {
  font-weight: bold;
}

h1 {
  font-size: 2rem;
  line-height: 36px;
  letter-spacing: -1px;
}

h2 {
  font-size: 1.25rem;
  line-height: 22px;
  letter-spacing: -0.63px;
}

h3 {
  font-size: 1rem;
  line-height: 24px;
  letter-spacing: -0.8px;
}

h4 {
  font-size: 0.75rem;
  line-height: 15px;
  letter-spacing: -0.25px;
}

p {
  margin-bottom: 26px;
}

.body-secondary {
  font-size: 0.6875rem;
  line-height: 18px;
  letter-spacing: -0.23px;
}

@keyframes openModalOverlay {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.ReactModal__Overlay,
.ReactModal__Overlay--after-open {
  z-index: 100000;
  animation-name: openModalOverlay;
  animation-duration: 0.35s;
  font-family: "Spartan", sans-serif;
  background-color: rgba(0, 0, 0, 0.75) !important; /* :( */
}

@keyframes openModalContent {
  from {
    top: 0;
    
  }
  to {
    top: none;
    
  }
}

.ReactModal__Content {
  animation-name: openModalContent;
  animation-duration: 0.35s;
}
